import { Component, OnInit } from '@angular/core';
import { ListCategoriesModule } from 'src/app/global/const/list-categories/list-categories.module';
import { CommerceService } from 'src/app/services/basecommerce/commerce.service';
import { CommerceModule } from 'src/app/models/basecommerce/commerce.module';
import { Router } from '@angular/router';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  //Variables
  loading:Boolean = false;
  geo:any = {latitude:null,longitude:null};  
  near = 5;  
  category:string;
  categs : ListCategoriesModule = new ListCategoriesModule();
  commerces : CommerceModule[] = [];
  commercesArray : CommerceModule[] = [];

  constructor(private _commerce:CommerceService,
              private _navigation:Router) { }

  ngOnInit() {
  }

  getLocation(_geo:any){
    this.geo = _geo;
    if(this.category)
      this.recargar(this.category);
  }

  getKilometros = function(lat2,lon2)
  {
      var rad = function(x) {return x*Math.PI/180;}
      var R = 6378.137; //Radio de la tierra en km
      var dLat = rad( lat2 - this.geo.latitude );
      var dLong = rad( lon2 - this.geo.longitude );
      var a = Math.sin(dLat/2) * Math.sin(dLat/2) + Math.cos(rad(this.geo.latitude)) * Math.cos(rad(lat2)) * Math.sin(dLong/2) * Math.sin(dLong/2);
      var c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1-a));
      var d = R * c;
      return d.toFixed(3);
  }

  getDistance(resp:CommerceModule[]){
    this.commerces = [];
    this.commercesArray = [];
    if(resp && resp.length > 0){
      resp.forEach(element => {        
        if(element.latitude && element.longitude){
          var rad = function(x) {return x*Math.PI/180;}
          var R = 6378.137; //Radio de la tierra en km
          var dLat = rad( parseFloat(element.latitude) - this.geo.latitude );
          var dLong = rad( parseFloat(element.longitude) - this.geo.longitude );
          var a = Math.sin(dLat/2) * Math.sin(dLat/2) + Math.cos(rad(this.geo.latitude)) * Math.cos(rad( parseFloat(element.latitude))) * Math.sin(dLong/2) * Math.sin(dLong/2);
          var c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1-a));
          var d = R * c;
          element.distance = parseInt(d.toFixed(3)) == 0 ? 1: parseInt(d.toFixed(3));
        }
        this.commercesArray.push(element);
        this.commerces.push(element);
      });
      this.commerces.sort(function(a, b) {
        return a.distance - b.distance;
      });
    }
  }

  recargar(category:string){    
    //this.loading = true;
    this.category = category;
    this._commerce.list(this.category)
    .subscribe((resp)=>{
      this.getDistance(resp);
      this.loading = false;
    })
  }

  filter(value:string){    
    this.commerces = this.commercesArray.filter(x =>
      x.name.toUpperCase().includes(value.toUpperCase())
      || (x.slogan && x.slogan.toUpperCase().includes(value.toUpperCase()))
      || (x.keywords && x.keywords.toUpperCase().includes(value.toUpperCase()))
      );
  }

  redirect(name:string){
    this._navigation.navigate(['/'+name]);
  }

}
