import { Injectable } from '@angular/core';
import { PersonaModel } from '../models/persona.model';
import { AngularFirestore, AngularFirestoreCollection, CollectionReference, fromCollectionRef } from '@angular/fire/firestore';
import { map, delay } from 'rxjs/operators';
import { Observable } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import * as firebase from 'firebase';
import {Query, QueryFn } from '@angular/fire/firestore';

@Injectable({
  providedIn: 'root'
})
export class PersonaService {

  private carpeta = 'img';  
  
  constructor(private http: HttpClient, private db: AngularFirestore) { }

  crear(persona:PersonaModel){
    return this.db
          .collection("persona")
          .add({...persona});        
  }


  actualizar(persona:PersonaModel){
  return this.db.collection('persona').doc(persona.id).set({
    ...persona
  },{
    merge:true
  });
  }


  guardarFoto(file:any, persona:PersonaModel):string{      
    const storageRef = firebase.storage().ref();

    var urlreturn = "";
    const uploadTask: firebase.storage.UploadTask = 
        storageRef.child(`${ this.carpeta }/${ file.name  }`)
          .put( file )

    uploadTask.on( firebase.storage.TaskEvent.STATE_CHANGED,
      ( snapshot:firebase.storage.UploadTaskSnapshot ) => {
        console.log('cargando imagen...');
      },
      ( error ) => console.error('Error al subir imagen',error),
      async () => {
        console.log('imagen cargada');        
        await uploadTask.snapshot.ref.getDownloadURL().then(res =>{
          urlreturn = res;
          persona.foto = res;
          this.actualizar(persona);
        });        
      }
    );
    return urlreturn;
  }  
  
  obtener(id:string){
    return this.db.collection('persona').doc(id)
    .valueChanges();
  }

  listar(){
    var apellido = false;
    var nombre = false;  

    return this.db.collection('persona', ref => {
      let query : Query = ref;
      if(apellido) {
       query = query.where('apellido', '==', 'veloza');
      }
      if(nombre) {
       query = query.where('nombre', '==', 'camilo');
      }
      return query;
    }).snapshotChanges()
    .pipe(
        map( this.procesar ),
        delay(500)
      );
  }

  eliminar(id:string){
    return this.db.collection("persona").doc(id).delete();
  }  

  private procesar( personaObj: object){    
    if(personaObj === null)
      return [];
    
    const personas : PersonaModel[] = [];

    Object.keys(personaObj).forEach(key =>{

      const item = personaObj[key].payload.doc.data() as PersonaModel;
      item.id = personaObj[key].payload.doc.id;    
      personas.push(item);
    });

    return personas;
  }
  
}