import { Injectable } from '@angular/core';
import { AngularFirestore } from 'angularfire2/firestore';
import { CatalogueModule, Cataloguegroup, Catalogueunit } from 'src/app/models/basecommerce/catalogue.module';
import { map } from 'rxjs/operators';
import * as firebase from 'firebase';
import {Query } from '@angular/fire/firestore';

@Injectable({
  providedIn: 'root'
})
export class CatalogueService {

  constructor(private db: AngularFirestore) { }

  update(catalogue:CatalogueModule){
    var obj = JSON.stringify({...catalogue});
    return this.db.collection('cataloguecommerce').doc(catalogue.id).set({
      obj},{merge:true});
  }
  
  updateGroup(group:Cataloguegroup, cat:string){
    console.log('actualiza');
    return this.db.collection('cataloguecommerce').doc(cat)
                  .collection('group').doc(group.id).set(
                    {...group}, {merge:true})
  }

  newGroup(group:Cataloguegroup, cat:string){
    console.log('crea');
    return this.db.collection('cataloguecommerce').doc(cat)
                  .collection('group').add({...group});
  }

  getGroups(id:string, active:boolean = true){
    return this.db.collection('cataloguecommerce').doc(id)
    .collection('group',
    ref => {
      let query : Query = ref;
      if(active) {
       query = query.where('status', '==', true);
      }      
      return query;
    })
    .snapshotChanges()
    .pipe(
        map( this.processGroup )
      );
  }

  private processGroup( groups: object){
    if(groups === null)
      return [];
    
    const res : Cataloguegroup[] = [];

    Object.keys(groups).forEach(key =>{

      const item = groups[key].payload.doc.data() as Cataloguegroup;
      item.id = groups[key].payload.doc.id;      
      res.push(item);
    });

    res.sort((a,b) => a.position - b.position);

    return res;
  }

  getUnits(id:string, group:string, active:boolean = true){
    return this.db.collection('cataloguecommerce').doc(id)
    .collection('group').doc(group)
    .collection('unit',
    ref => {
      let query : Query = ref;
      if(active) {
       query = query.where('status', '==', true);
      }      
      return query;
    })
    .snapshotChanges()
    .pipe(
        map( this.processUnits )
      );
  }

  updateUnit(unit:Catalogueunit, id:string, group:string){
    return this.db.collection('cataloguecommerce').doc(id)
                  .collection('group').doc(group)
                  .collection('unit').doc(unit.id).set(
                    {...unit}, {merge:true})
  }

  deleteUnit(unit:string, id:string, group:string){
    return this.db.collection("cataloguecommerce").doc(id)
    .collection('group').doc(group)
    .collection('unit').doc(unit).delete();
  } 

  newUnit(unit:Catalogueunit, id:string, group:string){
    return this.db.collection('cataloguecommerce').doc(id)
                  .collection('group').doc(group)
                  .collection('unit').add({...unit});
  }

  private processUnits( units: object){    
    if(units === null)
      return [];
    
    const res : Catalogueunit[] = [];

    Object.keys(units).forEach(key =>{

      const item = units[key].payload.doc.data() as Catalogueunit;
      item.id = units[key].payload.doc.id;    
      res.push(item);
    });

    res.sort((a,b) => a.name.localeCompare(b.name));

    return res;
  }

  saveUnitImage(file:any, unit:Catalogueunit, idCatalogue:string, idGroup:string){
    const storageRef = firebase.storage().ref();
    var urlreturn = "";

    var carpeta = "units";
    var name = unit.id;
    
    const uploadTask: firebase.storage.UploadTask = 
        storageRef.child(`${ carpeta }/${idCatalogue}/${ file.name  }`)
          .put( file )
        
          uploadTask.on( firebase.storage.TaskEvent.STATE_CHANGED,
            ( snapshot:firebase.storage.UploadTaskSnapshot ) => {
              console.log('cargando imagen...');
            },
            ( error ) => console.error('Error al subir imagen',error),
            async () => {
              console.log('imagen cargada');        
              await uploadTask.snapshot.ref.getDownloadURL().then(res =>{
                urlreturn = res;
                unit.image = res;
                this.updateUnit(unit, idCatalogue, idGroup);
              });        
            }
          );
          return urlreturn;        
  }
   
  getById(id:string){
    return this.db.collection('cataloguecommerce').doc(id)
    .valueChanges()
    .pipe(
      map(data => data as CatalogueModule || [])
    );
  }

  eliminar(id:string){
    return this.db.collection("cataloguecommerce").doc(id).delete();
  }

  private procesar( data){    
    if(data.length  <= 0){
      return [];
    }
    else{      
      var commerces : CatalogueModule;
      commerces = data[0].payload.doc.data() as CatalogueModule;
      commerces.id = data[0].payload.doc.id;    
      return commerces;
    }
  }

}
