import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { CommerceService } from 'src/app/services/basecommerce/commerce.service';
import { CommerceModule } from 'src/app/models/basecommerce/commerce.module';
import { DataCommerceModule } from 'src/app/models/basecommerce/datacommerce.module';
import { CatalogueModule, Cataloguegroup, Catalogueunit } from 'src/app/models/basecommerce/catalogue.module';
import { CatalogueService } from 'src/app/services/basecommerce/catalogue.service';
import { DataCommerceService } from 'src/app/services/basecommerce/datacommerce.service';

@Component({
  selector: 'app-products',
  templateUrl: './products.component.html',
  styleUrls: ['./products.component.css']
})
export class ProductsComponent implements OnInit {
  
  // Agregar dato a commerce //
  style:string;

  loading:boolean = false;
  loadingProd:boolean = false;

  commerce = new CommerceModule();
  morecommerces:CommerceModule[] = [];
  datacommerce = new DataCommerceModule();
  catalogue = new CatalogueModule();
  group = new Cataloguegroup();
  unit = new Catalogueunit();


  constructor(private _route:ActivatedRoute,
    private _navigation:Router,
    private _commerce:CommerceService,
    private _catalogue:CatalogueService,
    private _dataCommerce:DataCommerceService) { }

  ngOnInit() {
    this.loading = true;
    this.loadingProd = true;

    const pk = this._route.snapshot.paramMap.get('name');

    //get commerce
    this._commerce.getByName(pk)
      .subscribe((resp:CommerceModule[]) => {        
        if(resp.length > 0){

          this.commerce = resp[0];
          this.style = this.commerce.theme;
          this.catalogue.id = this.commerce.id;

          //get dataCommerce
          this._dataCommerce.getById(this.commerce.id)
            .subscribe((resp:DataCommerceModule) => {
              this.datacommerce = resp;              
              this.datacommerce.id = this.commerce.id;
              this.loading = false;
            });  

          //get groups
          this._catalogue.getGroups(this.commerce.id)
          .subscribe( (resp) => {
            this.catalogue.group = resp;
            if(this.catalogue.group){
              this.catalogue.group.forEach(element => {

                //get units
                this._catalogue.getUnits(this.commerce.id,element.id)
                .subscribe((resp) => {
                  element.units = resp; 
                });       
              });
            }
            this.loadingProd = false;
          }); 

          //
          this._commerce.listMore(this.commerce.idvalidation)
          .subscribe((resp) => {
              resp.forEach(element => {
                if(element.id != this.commerce.id)
                  this.morecommerces.push(element);
              });
          });          
          
        }else{
          this._navigation.navigate(['/home']);
        }
      })
  }

}
