import { Injectable } from '@angular/core';
import { AngularFirestore } from 'angularfire2/firestore';
import { SimpleListModule } from 'src/app/models/general/simpleList.module';
import { map } from 'rxjs/operators';
import {Query } from '@angular/fire/firestore';

@Injectable({
  providedIn: 'root'
})
export class SimpleListService {

  constructor(private db: AngularFirestore) { }
  

  update(data:SimpleListModule){
    return this.db.collection('simplelist').doc(data.name).set({
      ...data},{merge:true});
  }
    
  delete(name:string){
    return this.db.collection("simplelist").doc(name).delete();
  }

  getById(name:string){
    return this.db.collection('simplelist').doc(name)
    .valueChanges();
  }  

  list(){
    return this.db.collection('simplelist').snapshotChanges()
    .pipe(
        map( this.procesar )
      );
  }

  private procesar( data: object){    
    if(data === null)
      return [];
    
    var lists : SimpleListModule;
    
    lists = data[0].payload.doc.data() as SimpleListModule;
    lists.name = data[0].payload.doc.id;

    return lists;
  }
}
