import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { EquipoComponent } from './pages/equipo/equipo.component';
import { PersonaComponent } from './pages/persona/persona.component';
import { RegistryComponent } from './pages/management/registry/registry.component';
import { HomeadminComponent } from './pages/management/homeadmin/homeadmin.component';
import { CommerceadminComponent } from './pages/management/commerceadmin/commerceadmin.component';
import { AdminComponent } from './pages/management/admin/admin.component';
import { CommercecatalogueComponent } from './pages/management/commercecatalogue/commercecatalogue.component';
import { HomeComponent } from './pages/commerce/home/home.component';
import { ProductsComponent } from './pages/commerce/products/products.component';
import { UserdataComponent } from './pages/management/userdata/userdata.component';
import { ThemeComponent } from './pages/management/theme/theme.component';


const routes: Routes = [
  {path: 'registry', component : RegistryComponent },
  {path: 'homeadmin',component : HomeadminComponent },
  {path: 'commerceadmin',component : CommerceadminComponent },
  {path: 'admin',component : AdminComponent },
  {path: 'catalogueadmin',component : CommercecatalogueComponent },
  {path: 'theme',component : ThemeComponent },
  {path: 'user',component : UserdataComponent },
  {path: 'home', component : HomeComponent },
  {path: ':name', component : ProductsComponent},
  
  {path: '**', pathMatch:'full', redirectTo: 'home' },
  
  {path: 'equipo',component : EquipoComponent },
  {path: 'persona/:id',component : PersonaComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
