import { Injectable } from '@angular/core';
import { AngularFirestore } from 'angularfire2/firestore';
import { ValidationModule } from 'src/app/models/basecommerce/validation.module';
import { map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class ValidationService {

  constructor(private db: AngularFirestore) { }

  new(validation:ValidationModule){
    return this.db
          .collection("validationcommerce")
          .add({...validation});
  }

  update(data:ValidationModule){
    return this.db.collection('validationcommerce').doc(data.id).set({
      ...data},{merge:true});
  }
  
  getById(id:string){
    return this.db.collection('validationcommerce').doc(id)
    .valueChanges();
  }  

  eliminar(id:string){
    return this.db.collection("validationcommerce").doc(id).delete();
  }

  validateEmail(email:string){
    return this.db.collection('validationcommerce',
      ref => ref.where('email','==',email)).snapshotChanges();
  }

  validate(user:string, password:string){
    return this.db.collection('validationcommerce',
      ref => ref.where('email','==',user).where('password','==',password)).snapshotChanges()
      .pipe( map( this.procesar ));
  }

  private procesar( data){    
    if(data.length  <= 0){
      return [];
    }
    else{      
      var commerces : ValidationModule;          
      commerces = data[0].payload.doc.data() as ValidationModule;
      commerces.id = data[0].payload.doc.id;    
  
      return commerces;
    }
  }


}
