import { Component, OnInit } from '@angular/core';
import { CatalogueModule, Cataloguegroup, Catalogueunit } from 'src/app/models/basecommerce/catalogue.module';
import { CatalogueService } from 'src/app/services/basecommerce/catalogue.service';
import { AuthService } from 'src/app/services/general/auth.service';
import { ImagesService } from 'src/app/services/general/images.service';
import { Router } from '@angular/router';
import { NgForm } from '@angular/forms';
import Swal from 'sweetalert2';
import * as $ from 'jquery';


@Component({
  selector: 'app-commercecatalogue',
  templateUrl: './commercecatalogue.component.html',
  styleUrls: ['./commercecatalogue.component.css']
})
export class CommercecatalogueComponent implements OnInit {
  
  //Validacion
  idValidation:string;
  idCommerce:string;
  idGroup:string;
  loading:boolean = false;
  //Objeto completo
  catalogue = new CatalogueModule();
  //objetos auxiliares grupo
  group = new Cataloguegroup();
  group2 = new Cataloguegroup();
  //objeto unidad
  unit = new Catalogueunit();
  indexUnit:number;
  //images
  fileData: File = null;

  constructor(private _catalogue:CatalogueService,
              private _auth:AuthService,
              private _image:ImagesService,
              private _navigation:Router) { }

  ngOnInit() {    
    this.loading = true;
     this.idCommerce = this._auth.commerceData();
     this.idValidation = this._auth.userData();    
    
     if(this.idValidation == null)
       this._navigation.navigate(['/admin']);
     else if(this.idCommerce == null)
       this._navigation.navigate(['/admin']);
  
    this.catalogue.id = this.idCommerce;

    this._catalogue.getGroups(this.idCommerce, false)
    .subscribe( (resp) => {
      this.catalogue.group = resp;
      this.loading = false;
      if(this.catalogue.group){
        this.catalogue.group.forEach(element => {
          this._catalogue.getUnits(this.idCommerce,element.id, false)
          .subscribe((resp) => {
            element.units = resp;            
            console.log(element);
          });       
        });
      }
     });      
  }

  saveCategory(form:NgForm){
    if(form.invalid){
      Object.values( form.controls ).forEach( control => {
        control.markAsTouched();
      });
      return;
    };
    
    if(!this.catalogue.group) 
      this.catalogue.group = [];

    Swal.fire({
      title: 'Espere',
      text: 'Guardando información',
      allowOutsideClick: false,      
    });
    Swal.showLoading();

    let request;
    if(this.group.id)
      request = this._catalogue.updateGroup(this.group, this.idCommerce);
    else{
      this.group.position = this.catalogue.group.length > 0 ? this.catalogue.group.length : 1;
      this.group.status = true;
      request = this._catalogue.newGroup(this.group, this.idCommerce);
    }

    request.then(resp => {
        if(!this.group.id){
          this.group.id = resp;
          //this.catalogue.group.push(this.group);
        }
        $("#close").click();
        Swal.fire({
          title: 'Bien!',
          text: 'Registro guardado',
        });
      });    
  }

  editgroup(index){
    if(index != null)
      this.group = {...this.catalogue.group[index]};  
    else
      this.group = new Cataloguegroup();    
  }

  addUnit(index){
    this.unit = new Catalogueunit();
    this.group = this.catalogue.group[index];
  }  

  editunit(idgrupo, idunit){
    this.group = this.catalogue.group[idgrupo];
    this.unit = {...this.group.units[idunit]};
  }

  deleteunit(idgrupo, idunit){
    this.group = this.catalogue.group[idgrupo];
    this.unit = {...this.group.units[idunit]};
    Swal.fire({
      title: '¿Estás seguro?',
      text: `¿Estás seguro  de eliminar ${this.unit.name}?`,
      showConfirmButton: true, 
      showCancelButton: true
    }).then((resp) => {
      if(resp.value){
        Swal.fire({
          title: 'Espere',
          text: 'Eliminando información',
          allowOutsideClick: false,      
        });
        Swal.showLoading();
        this._catalogue.deleteUnit(this.unit.id, this.catalogue.id,  this.group.id)
        .then(function() {
            Swal.fire({
              title: 'OK',
              text: 'Registro eliminado',
            });
            this._navigation.navigate(['/equipo']);
          }).catch(function(error) {
            console.error("Error removing document: ", error);
        });          

      }
    }); 
  }

  saveUnit(form:NgForm){
    if(form.invalid){
      Object.values( form.controls ).forEach( control => {
        control.markAsTouched();
      });
      return;
    };

    if(!this.group.units) 
      this.group.units = [];

    Swal.fire({
      title: 'Espere',
      text: 'Guardando información',
      allowOutsideClick: false,      
    });
    Swal.showLoading();

    let request;
    if(this.unit.id) 
      request = this._catalogue.updateUnit(this.unit, this.catalogue.id,  this.group.id);
    else{
      this.unit.status = true;
      request = this._catalogue.newUnit(this.unit, this.catalogue.id, this.group.id);
    }

    request.then(resp => {
        if(!this.unit.id){
          this.unit.id = resp;
        }
        $("#closeunit").click();
        Swal.fire({
          title: 'Bien!',
          text: 'Registro guardado',
        });
      });    
        
  }

  loadImage(id:string){
    $("#"+id+"").click();
  }
  
  fileProgress(fileInput: any, idgrupo, idunit) {
    this.fileData = <File>fileInput.target.files[0];
    this.group = this.catalogue.group[idgrupo];
    this.unit = {...this.group.units[idunit]};
    this.unit.image = this._catalogue.saveUnitImage(this.fileData, this.unit, this.catalogue.id, this.group.id);
  }

  guardar(form:NgForm){
    try{

      if(form.invalid){      
        return;
      }

      this.catalogue.group.push(this.group);

      Swal.fire({
        title: 'Espere',
        text: 'Guardando información',
        allowOutsideClick: false,      
      });
      Swal.showLoading();
  
      let request = this._catalogue.update(this.catalogue);
      request.then(resp => {
        Swal.fire({
          title: 'Bien!',
          text: 'Registro guardado',
        });
      });
    }catch
    {
      Swal.fire({
        title: 'Error',
        text: 'Intentalo nuevamente',
      });
    }    
  }

  volver(){
    this._auth.commerceLogout();      
    this._navigation.navigate(['/homeadmin']);
  }

  up(index){
    this.group = {...this.catalogue.group[index]};
    this.group2 = {...this.catalogue.group[index-1]}
    var pre = this.group.position;
    this.group.position = this.group2.position;
    this.group2.position = pre;
    
    this._catalogue.updateGroup(this.group, this.idCommerce);
    this._catalogue.updateGroup(this.group2, this.idCommerce);
        
  }
  
  down(index){
    if(index == this.catalogue.group.length-1)
      return;        
    this.group = {...this.catalogue.group[index]};
    this.group2 = {...this.catalogue.group[index+1]}
    var pre = this.group.position;
    this.group.position = this.group2.position;
    this.group2.position = pre;

    this._catalogue.updateGroup(this.group, this.idCommerce);
    this._catalogue.updateGroup(this.group2, this.idCommerce);
  }

  check(index){
    this.group = {...this.catalogue.group[index]};
    this.group.status = !this.group.status;
    this._catalogue.updateGroup(this.group, this.idCommerce);
  }

  checkUnit(idgrupo, idunit){
    this.group = this.catalogue.group[idgrupo];
    this.unit = {...this.group.units[idunit]};
    console.log(this.unit.status);
    this.unit.status = !this.unit.status;
    this._catalogue.updateUnit(this.unit, this.catalogue.id, this.group.id);
  }

}
