import { Component, OnInit } from '@angular/core';
import { CommerceService } from 'src/app/services/basecommerce/commerce.service';
import { AuthService } from 'src/app/services/general/auth.service';
import { Router } from '@angular/router';
import { CommerceModule } from 'src/app/models/basecommerce/commerce.module';
import { ImagesService } from 'src/app/services/general/images.service';
import { NgForm } from '@angular/forms';
import Swal from 'sweetalert2';
import { SimpleListModule } from 'src/app/models/general/simpleList.module';
import { SimpleListService } from 'src/app/services/general/simple-list.service';
import { DataCommerceService } from 'src/app/services/basecommerce/datacommerce.service';
import { DataCommerceModule } from 'src/app/models/basecommerce/datacommerce.module';

@Component({
  selector: 'app-commerceadmin',
  templateUrl: './commerceadmin.component.html',
  styleUrls: ['./commerceadmin.component.css']
})
export class CommerceadminComponent implements OnInit {

  idValidation:string;
  loading:boolean = false;
  commerce = new CommerceModule();
  datacommerce = new DataCommerceModule();

  commerceValid = new CommerceModule();
  valid:boolean = false;
  
  categories = new SimpleListModule();
  cities = new SimpleListModule();

  constructor(private _commerce:CommerceService,
              private _datacommerce:DataCommerceService,              
              private _image:ImagesService,
              private _list:SimpleListService,
              private _navigation:Router,
              private _auth:AuthService) { }

  ngOnInit() {       
    
    this.loading = true;

    const id = this._auth.commerceData();
    this.idValidation = this._auth.userData();    
    
    if(this.idValidation == null)
      this._navigation.navigate(['/admin']);
          
    this._list.getById('category')
        .subscribe((resp:SimpleListModule) => {
        this.categories = resp;
    });

    this._list.getById('city')
        .subscribe((resp:SimpleListModule) => {
        this.cities = resp;  
    });    

    if(id != null){
      var req = this._commerce.getById(id);

      req.subscribe((resp:CommerceModule) => {
          this.commerce = resp;
          this.commerce.id = id;       
          if(this.commerce.uniqueName){
            this.valid = true;
            this.commerceValid = {...this.commerce};
          }
      });

      var req2 = this._datacommerce.getById(id);

      req2.subscribe((resp:DataCommerceModule) => {
          this.datacommerce = resp;
          this.datacommerce.id = id;
          this.loading = false;
      });      
    }else{
      this.loading = false;
    }        
    this.commerce.idvalidation = this.idValidation;
  }
  
  loadImage(fileInput: any) {
    var fileData: File = null; 
    fileData = <File>fileInput.target.files[0];    
    this._image.saveCommerceLogo(fileData, this.commerce);
  }

  guardar(form:NgForm){
    if(form.invalid)
      return;   

    if(!this.valid)
      return

    Swal.fire({
      title: 'Espere',
      text: 'Guardando información',
      allowOutsideClick: false,      
    });
    Swal.showLoading();    


    if(!this.commerce.theme)
      this.commerce.theme = 'basic';

    if(this.commerce.latitude)
      this.commerce.latitude = this.commerce.latitude.toString().replace(',','.');
    if(this.commerce.longitude)
      this.commerce.longitude = this.commerce.longitude.toString().replace(',','.');      

    let request;
    
    if(this.commerce.id != null)
      request = this._commerce.update(this.commerce);      
    else{
      this.commerce.theme = "basic";
      request = this._commerce.new(this.commerce);
    }

    request.then(resp => {            
            this.commerce.id = this.commerce.id == null? resp.id: this.commerce.id;
            this.datacommerce.id = this.commerce.id;

            let request;                      
            request = this._datacommerce.update(this.datacommerce);
            
            request.then(resp => {        
            this._auth.userLogin(resp.id);
            Swal.fire({
              title: this.commerce.name,
              text: 'Registro guardado',
            });
            this._navigation.navigate(['/homeadmin']);
          })


            Swal.fire({
            title: this.commerce.name,
            text: 'Registro guardado',                    
        })
    })

  }

  volver(){
    this._auth.commerceLogout();      
    this._navigation.navigate(['/homeadmin']);
  }

  catalogo(){
    this._navigation.navigate(['/catalogueadmin']);
  }

  validatename(value:string){
    if(!value){
      this.valid = false;
      return;
    }

    value = value.trim();

    if(value == this.commerceValid.uniqueName){
      this.valid = true;
      return;
    }

    this._commerce.getByName(value)
      .subscribe((resp)=>{
        if(resp.length > 0 && resp[0].id){
          this.valid = false;
          return;
        }else{
          this.valid = true;
          return;
        }
    })
  }

}
