import { Component, OnInit } from '@angular/core';
import { ValidationModule } from 'src/app/models/basecommerce/validation.module';
import { AuthService } from 'src/app/services/general/auth.service';
import { Router } from '@angular/router';
import { ValidationService } from 'src/app/services/basecommerce/validation.service';
import { NgForm } from '@angular/forms';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-userdata',
  templateUrl: './userdata.component.html',
  styleUrls: ['./userdata.component.css']
})
export class UserdataComponent implements OnInit {

  loading:boolean = false;
  validation:ValidationModule = new ValidationModule();
  confirm = new ValidationModule();
  password2:string;

  constructor(private _validation:ValidationService,
    private _auth:AuthService,
    private _navigation:Router) { }

  ngOnInit() {
    this.loading = true;
    var id = this._auth.userData();
    if(id != null){
      this._validation.getById(id)
        .subscribe( (resp:ValidationModule) => {
          this.validation = resp;
          this.validation.id = id;          
          this.loading = false;
        });  
    }
  }

  guardar(form:NgForm){
    if(form.invalid)
      return;   
    Swal.fire({
      title: 'Espere',
      text: 'Guardando información',
      allowOutsideClick: false,      
    });
    Swal.showLoading();    

    let request = this._validation.update(this.validation);  
    request.then(resp => {                
            Swal.fire({
            title: this.validation.name,
            text: 'Registro guardado',                    
        })
    })
  }

}
