import { Component, OnInit } from '@angular/core';
import { CommerceService } from 'src/app/services/basecommerce/commerce.service';
import { AuthService } from 'src/app/services/general/auth.service';
import { Router } from '@angular/router';
import { CommerceModule } from 'src/app/models/basecommerce/commerce.module';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-theme',
  templateUrl: './theme.component.html',
  styleUrls: ['./theme.component.css']
})
export class ThemeComponent implements OnInit {


  commerce = new CommerceModule();
  loading:boolean = false;

  constructor(private _commerce:CommerceService,
    private _auth:AuthService,
    private _navigation:Router) { }

  ngOnInit() {
    this.loading = true;

    const id = this._auth.commerceData();
       
    if(!id)
      this._navigation.navigate(['/admin']);

    var req = this._commerce.getById(id);

    req.subscribe((resp:CommerceModule) => {
          this.commerce = resp;
          this.commerce.id = id;
          this.loading = false;
    });
  }

  save(value){
    this.commerce.theme = value;

    Swal.fire({
      title: 'Espere',
      text: 'Guardando información',
      allowOutsideClick: false,      
    });
    Swal.showLoading();   

    let request;                      
    request = this._commerce.update(this.commerce);
            
    request.then(resp => {        
      Swal.fire({
        title: this.commerce.name,
        text: 'Listo!'
      });
      this._navigation.navigate(['/homeadmin']);
    });
  }

}
