import { Injectable } from '@angular/core';
import { AngularFirestore } from '@angular/fire/firestore';
import { CommerceModule } from 'src/app/models/basecommerce/commerce.module';
import { map, delay } from 'rxjs/operators';
import { Observable } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import * as firebase from 'firebase';
import {Query, QueryFn } from '@angular/fire/firestore';


@Injectable({
  providedIn: 'root'
})
export class CommerceService {

  constructor(private db: AngularFirestore) { }
  
  new(commerce:CommerceModule){
    return this.db
          .collection("commerce")
          .add({...commerce});
  }

  update(commerce:CommerceModule){
    return this.db.collection('commerce').doc(commerce.id).set({
      ...commerce},{merge:true});
  }
  
  getById(id:string){
    return this.db.collection('commerce').doc(id)
    .valueChanges();
  }

  getByName(name:string){
    return this.db.collection('commerce',
      ref => ref.where('uniqueName','==',name)).snapshotChanges()
      .pipe( map( this.procesar ));
  }
  

  listPrivate(idValidation:string){    
    return this.db.collection('commerce', ref => ref.where('idvalidation','==',idValidation)).snapshotChanges()
    .pipe(
        map( this.procesar )
      );
  }

  
  listMore(idValidation:string){    
    return this.db.collection('commerce', ref => ref
      .where('idvalidation','==',idValidation)).snapshotChanges().pipe(
        map( this.procesar )
      );
  }

  list(category:string = ''){
    
    return this.db.collection('commerce', ref => {
      let query : Query = ref;
      if(category) {
       query = query.where('category', '==', category);
      }
      return query;
    }).snapshotChanges()
    .pipe(
        map( this.procesar )
      );
  }

  eliminar(id:string){
    return this.db.collection("commerce").doc(id).delete();
  }  

  private procesar( data: object){    
    if(data === null)
      return [];
    
    const commerces : CommerceModule[] = [];

    Object.keys(data).forEach(key =>{

      const item = data[key].payload.doc.data() as CommerceModule;
      item.id = data[key].payload.doc.id;
      commerces.push(item);
    });

    return commerces;
  }
   
}
