import { Injectable } from '@angular/core';
import * as firebase from 'firebase';
import { imagefolder } from 'src/app/global/enums/enums/enums.module';
import { CommerceModule } from 'src/app/models/basecommerce/commerce.module';

@Injectable({
  providedIn: 'root'
})
export class ImagesService {  

  constructor() { }
  
  saveCommerceLogo(file:any, commerce:CommerceModule){      

    var carpeta = 'commerceLogo';
    var name = commerce.id;
    
    const storageRef = firebase.storage().ref();

    var urlreturn = "";
    
    const uploadTask: firebase.storage.UploadTask = 
        storageRef.child(`${ carpeta }/${ name }`).put( file );

    uploadTask.on( firebase.storage.TaskEvent.STATE_CHANGED,
      ( snapshot:firebase.storage.UploadTaskSnapshot ) => {
        //console.log('cargando imagen...');
      },
      ( error ) => console.error('Error al subir imagen',error),
      async () => {
        // console.log('imagen cargada');        
        await uploadTask.snapshot.ref.getDownloadURL().then(res =>{
          commerce.logo = res;
        });        
      }
    );
  } 

}
