import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { PersonaService } from 'src/app/services/persona.service';
import Swal from 'sweetalert2';
import { Observable } from 'rxjs';
import { ActivatedRoute, RouterLink, Router } from '@angular/router';
import { HttpClient } from '@angular/common/http';
import { ValidationService } from 'src/app/services/basecommerce/validation.service';
import { CommerceService } from 'src/app/services/basecommerce/commerce.service';
import { ValidationModule } from 'src/app/models/basecommerce/validation.module';
import { CommerceModule } from 'src/app/models/basecommerce/commerce.module';
import { AuthService } from 'src/app/services/general/auth.service';


@Component({
  selector: 'app-registry',
  templateUrl: './registry.component.html',
  styleUrls: ['./registry.component.css']
})
export class RegistryComponent implements OnInit {
  
  validation = new ValidationModule();
  confirm = new ValidationModule();
  password2:string;

  constructor(private _validation:ValidationService,
              private _commerce:CommerceService,
              private _auth:AuthService,
              private _navigation:Router) { }

  
  ngOnInit(){}
  
  
  guardar(form:NgForm){  
      
    try{
      if(form.invalid){
        Object.values( form.controls ).forEach( control => {
          control.markAsTouched();
        });
        return;
      }

      Swal.fire({
        title: 'Espere',
        text: 'Guardando información',
        allowOutsideClick: false,      
      });
      Swal.showLoading();    

      let req = this._validation.validateEmail(this.validation.email)
        .subscribe(snapshot => {
          console.log(snapshot.length);
        if(snapshot.length > 0){
          Swal.fire({
            title: this.validation.email,
            text: 'El correo ya está registrado'
          })          
        }else{                    
          req.unsubscribe();
          let request;                      
          request = this._validation.new(this.validation);
          request.then(resp => {        
            this._auth.userLogin(resp.id);
            Swal.fire({
              title: this.validation.name,
              text: 'Registro guardado',
            });
            this._navigation.navigate(['/homeadmin']);
          })
        }
      })
    }
    catch{
      Swal.fire({
        title: 'Error',
        text: 'intentalo nuevamente'
      })
    }    
  }

}
