import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

import { map } from 'rxjs/operators';
import { ValidationModule } from 'src/app/models/basecommerce/validation.module';

@Injectable({
  providedIn: 'root'
})
export class AuthService {

  constructor() {
  }

  userLogin( user: string ) { localStorage.setItem('hash', user); }
  userData() { return localStorage.getItem('hash') ? localStorage.getItem('hash') : null; }
  userLogout(){localStorage.removeItem('hash');}

  commerceLogin( commerce: string ) { localStorage.setItem('hash2', commerce); }
  commerceData() { return localStorage.getItem('hash2') ? localStorage.getItem('hash2') : null; }
  commerceLogout( ) { localStorage.removeItem('hash2'); }

  logout() {
    localStorage.removeItem('hash');
    localStorage.removeItem('hash2');
  }
  
}
