import { Component, OnInit } from '@angular/core';
import { ValidationModule } from 'src/app/models/basecommerce/validation.module';
import { ValidationService } from 'src/app/services/basecommerce/validation.service';
import { AuthService } from 'src/app/services/general/auth.service';
import { Router } from '@angular/router';
import Swal from 'sweetalert2';
import { NgForm } from '@angular/forms';

@Component({
  selector: 'app-admin',
  templateUrl: './admin.component.html',
  styleUrls: ['./admin.component.css']
})
export class AdminComponent implements OnInit {

  validation = new ValidationModule();

  constructor(private _validation:ValidationService,
    private _auth:AuthService,
    private _navigation:Router) { }

  ngOnInit() {
    this._auth.logout();
  }

  guardar(form:NgForm){  
      
    try{
      if(form.invalid){
        Object.values( form.controls ).forEach( control => {
          control.markAsTouched();
        });
        return;
      }

      Swal.fire({
        title: 'Espere...',
        text: 'Comprobando informacaión',
        allowOutsideClick: false,      
      });
      Swal.showLoading();    
      
      let request;                      
      request = this._validation.validate(this.validation.email, this.validation.password)
        .subscribe( (resp:ValidationModule) => {
          if(resp.id != null){
            //Acceso
            this._auth.userLogin(resp.id);
            this._navigation.navigate(['/homeadmin']);
            Swal.close();
          }else{
            Swal.fire({
              title: 'Error',
              text: 'Usuario o clave incorrectos'
            })
          }
        });          
    }
    catch{
      Swal.fire({
        title: 'Error',
        text: 'intentalo nuevamente'
      })
    }    
  }

}
