import { Component, OnInit } from '@angular/core';
import { CommerceService } from 'src/app/services/basecommerce/commerce.service';
import { AuthService } from 'src/app/services/general/auth.service';
import { CommerceModule } from 'src/app/models/basecommerce/commerce.module';
import { Router } from '@angular/router';

@Component({
  selector: 'app-homeadmin',
  templateUrl: './homeadmin.component.html',
  styleUrls: ['./homeadmin.component.css']
})
export class HomeadminComponent implements OnInit {

  commerces:CommerceModule[] = [];
  loading:boolean = true;

  constructor(private _commerce:CommerceService,
              private _auth:AuthService,
              private _navigation:Router) { }

  ngOnInit() {
    var id = this._auth.userData();
    if(id == null)
      this._navigation.navigate(['/admin']);
      
    this._auth.commerceLogout();
    if(id != null){
      this._commerce.listPrivate(id)
        .subscribe( (resp) => {
          this.commerces = resp;
          this.loading = false;
        });  
    }
  }

  goToCommerce(id:string = null){    
    if(id !== null)
      this._auth.commerceLogin(id);
    this._navigation.navigate(['/commerceadmin']);
  }

  goToTheme(id:string = null){    
    if(id !== null)
      this._auth.commerceLogin(id);
    this._navigation.navigate(['/theme']);
  }
  
  goToCatalogue(id:string){
    this._auth.commerceLogin(id);
    this._navigation.navigate(['/catalogueadmin']);
  }

}
