import { Injectable } from '@angular/core';
import { AngularFirestore } from 'angularfire2/firestore';
import { DataCommerceModule } from 'src/app/models/basecommerce/datacommerce.module';

@Injectable({
  providedIn: 'root'
})
export class DataCommerceService {

  constructor(private db: AngularFirestore) { }

  update(datacommerce:DataCommerceModule){
    return this.db.collection('datacommerce').doc(datacommerce.id).set({
      ...datacommerce},{merge:true});
  }
  
  getById(id:string){
    return this.db.collection('datacommerce').doc(id)
    .valueChanges();
  }  

  eliminar(id:string){
    return this.db.collection("datacommerce").doc(id).delete();
  }

}
