import { Component, OnInit } from '@angular/core';
import { PersonaModel } from 'src/app/models/persona.model';
import { NgForm } from '@angular/forms';
import { PersonaService } from 'src/app/services/persona.service';
import Swal from 'sweetalert2';
import { Observable } from 'rxjs';
import { ActivatedRoute, RouterLink, Router } from '@angular/router';
import { HttpClient } from '@angular/common/http';


@Component({
  selector: 'app-persona',
  templateUrl: './persona.component.html',
  styleUrls: ['./persona.component.css']
})
export class PersonaComponent implements OnInit {
  
  persona = new PersonaModel();
  fileData: File = null;
  req = null;

  constructor(private _servicio:PersonaService,
              private _route:ActivatedRoute,
              private _navigation:Router) { }

  ngOnInit() {        
    const id = this._route.snapshot.paramMap.get('id')

    if(id !== 'nuevo'){
      this.req = this._servicio.obtener(id);
      this.req
        .subscribe((resp:PersonaModel) => {
          this.persona = resp;
          this.persona.id = id;
        })
    }
  }


  fileProgress(fileInput: any) {

    fileData: File = null; 

    this.fileData = <File>fileInput.target.files[0];
    
    this.persona.foto = this._servicio.guardarFoto(this.fileData, this.persona);
  }

  guardar(form:NgForm){
    if(form.invalid){      
      return;
    }    

    Swal.fire({
      title: 'Espere',
      text: 'Guardando información',
      allowOutsideClick: false,      
    });
    Swal.showLoading();    

    let request;
    
    if(this.persona.id != null)
      request = this._servicio.actualizar(this.persona);      
    else
      request = this._servicio.crear(this.persona);

    request.then(resp => {      
            this.persona.id = this.persona.id == null? resp.id: this.persona.id;
            Swal.fire({
            title: this.persona.nombre,
            text: 'Registro guardado',                    
        })
    })

  }

  borrar(){        
    Swal.fire({
      title: '¿Estás seguro?',
      text: `¿Estás seguro  de eliminar a ${this.persona.nombre}?`,
      showConfirmButton: true, 
      showCancelButton: true
    }).then((resp) => {
      if(resp.value){
        Swal.fire({
          title: 'Espere',
          text: 'Eliminando información',
          allowOutsideClick: false,      
        });
        Swal.showLoading();
        this._servicio.eliminar(this.persona.id)
        .then(function() {
            Swal.fire({
              title: this.persona.nombre,
              text: 'Registro eliminado',
            });
            this._navigation.navigate(['/equipo']);
          }).catch(function(error) {
            console.error("Error removing document: ", error);
        });          

      }
    });    
  }
}
